.skillArea {
  /* border: 1px solid #000; */
  justify-content: center;
  align-items: center;
}
.skillTitle {
  margin-bottom: 5rem;
}
.skillBox {
  /* border: 1px solid red; */
}
.skillBox>ul {
  /* border: 1px solid yellow; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0;
}
.skillBox>ul>li {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.3rem;
}
.abilityTitle {
  width: 150px;
  color: #fff;
  font-weight: bold;
}

.abilityPercent {
  display: flex;
  justify-content: flex-end;
  height: 25px;
  box-shadow: 0px 0px 5px #fff;
  background: lightblue;
  animation: percent 1s linear;
}
.percentFront{
  padding-right: 1rem;
  border-radius: 0  20px 20px 0;
  animation: percentText 1s linear;
  color: #fff;
}

@keyframes percent {
  0% {
    width: 10%;
  }
  100% {
    width: 100%;
  }
}
@keyframes percentText {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.percentBar {
  width: 750px;
  background: #ccc;
}
.contentBox {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}