* {
  box-sizing: border-box;
  list-style: none;
}
.mainNav {
  z-index: 2;
  position: fixed;
  top: 0;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mainNav>div {
  border: 1px solid red;
  width: 30%;
  height: 100%;
}
.menuList{
  display: flex;
  justify-content: center;
  width: 70%;
  height: 100%;
  background-color: #efefef;
  box-shadow: 0px 2px 15px #666;
  border-radius: 0px 0px 10px 10px;
}
.mainNav>ul>li {
  width: 20%;
  margin-left: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}